// CustomTree.js
import React, { useCallback, useEffect, useState } from 'react';
import TreeComponent from '../../../customizedComponents/TreeComponent';
import { createGetRequest } from '../../../../utility/services/requests';

const CustomTree = () => {
  const [data, setData] = useState([]);
  const [expanded, setExpanded] = useState({});
  const [selectedNodes, setSelectedNodes] = useState(new Set(['1', '1-2']));
  const [showPopup, setShowPopup] = useState(false);
  const [currentNode, setCurrentNode] = useState(null);
  const [nodeName, setNodeName] = useState('');
  const [nodeValue, setNodeValue] = useState('');
  const [showDeleteConfirmation, setShowDeleteConfirmation] = useState(false);

  const [showAdd, setShowAdd] = useState(false);
  const [showEdit, setShowEdit] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [showCheckbox, setShowCheckbox] = useState(false);
  const showValues = false;
  const nodeField = 'displayname';
  const fetchData = useCallback(() => {
    
       createGetRequest({ endPoint: `/Haj/HajPermissionGrant/GetPermissionsTree` })({
         onSuccess: (response) => { 
          setData(response)
         },
       });
     
   }, []);

   useEffect(() => {
    fetchData()
   }, []);
  // دالة لتبديل حالة التوسيع/الطي
  const toggleExpand = (nodeId) => {
    setExpanded((prevExpanded) => ({
      ...prevExpanded,
      [nodeId]: !prevExpanded[nodeId],
    }));
  };

  // دالة لإضافة عقدة جديدة
  const addNode = (parentId) => {
    const newNode = {
      id: `new-${Date.now()}`,
      [nodeField]: '',
      value: '',
      children: [],
    };

    const addNodeRecursive = (nodes) => {
      return nodes.map((node) => {
        if (node[nodeField] === parentId) {
          return {
            ...node,
            children: [...node.children, newNode],
          };
        } else if (node.children) {
          return {
            ...node,
            children: addNodeRecursive(node.children),
          };
        }
        return node;
      });
    };

    setData(addNodeRecursive(data));
    setShowPopup(true);
    setCurrentNode(newNode);
  };

  // دالة لتعديل العقدة
  const editNode = (node) => {
    setShowPopup(true);
    setCurrentNode(node);
    setNodeName(node[nodeField]);
    setNodeValue(node.value);
  };

  // دالة لحذف العقدة بعد التأكد
  const deleteNode = (nodeId) => {
    const node = findNodeById(data, nodeId);
    if (node.children.length > 0) {
      alert('Cannot delete node with children.');
      return;
    }

    const deleteNodeRecursive = (nodes) => {
      return nodes.filter((node) => {
        if (node[nodeField] === nodeId) return false;
        if (node.children) {
          node.children = deleteNodeRecursive(node.children);
        }
        return true;
      });
    };

    setData(deleteNodeRecursive(data));
    setShowDeleteConfirmation(false);
  };

  // دالة لإيجاد العقدة بواسطة ID
  const findNodeById = (nodes, id) => {
    for (let node of nodes) {
      if (node[nodeField] === id) return node;
      if (node.children) {
        const result = findNodeById(node.children, id);
        if (result) return result;
      }
    }
    return null;
  };

  // دالة لتحديد العقدة أو إلغاء تحديدها
  const toggleSelectNode = (nodeId) => {
    setSelectedNodes((prevSelected) => {
      const newSelected = new Set(prevSelected);

      if (newSelected.has(nodeId)) {
        newSelected.delete(nodeId);
        removeChildNodes(nodeId, newSelected);
      } else {
        newSelected.add(nodeId);
        selectParentNodes(nodeId, newSelected);
      }

      return newSelected;
    });
  };

  // دالة لتحديد العقد الأبويّة للعقدة
  const selectParentNodes = (nodeId, newSelected) => {
    const node = findNodeById(data, nodeId);
    if (node) {
      const parentNode = findParentNode(data, nodeId);
      if (parentNode && !newSelected.has(parentNode.id)) {
        newSelected.add(parentNode.id);
        selectParentNodes(parentNode.id, newSelected);
      }
    }
  };

  // دالة لإيجاد العقدة الأبويّة (Parent Node) للعقدة المحددة
  const findParentNode = (nodes, nodeId) => {
    for (let node of nodes) {
      for (let child of node.children) {
        if (child.id === nodeId) {
          return node;
        }
      }
      const parent = findParentNode(node.children, nodeId);
      if (parent) return parent;
    }
    return null;
  };

  // دالة لإزالة تحديد العقد الأبويّة فقط إذا لم يكن لديهم أبناء مختارين
  const removeParentNodes = (nodeId, newSelected) => {
    const parentNode = findParentNode(data, nodeId);
    if (parentNode && newSelected.has(parentNode.id)) {
      const hasSelectedChildren = parentNode.children.some((child) => newSelected.has(child.id));
      if (!hasSelectedChildren) {
        newSelected.delete(parentNode.id);
        removeParentNodes(parentNode.id, newSelected);
      }
    }
  };

  // دالة لإزالة تحديد الأبناء
  const removeChildNodes = (nodeId, newSelected) => {
    const node = findNodeById(data, nodeId);
    if (node) {
      newSelected.delete(node[nodeField]);

      const removeChildrenRecursive = (children) => {
        children.forEach((child) => {
          newSelected.delete(child.id);
          if (child.children && child.children.length > 0) {
            removeChildrenRecursive(child.children);
          }
        });
      };

      removeChildrenRecursive(node.children);
    }
  };

  // دالة لحفظ التعديلات
  const saveNode = () => {
    const updateNodeRecursive = (nodes) => {
      return nodes.map((node) => {
        if (node[nodeField] === currentNode.id) {
          return { ...node, [nodeField]: nodeName, value: nodeValue };
        } else if (node.children) {
          return {
            ...node,
            children: updateNodeRecursive(node.children),
          };
        }
        return node;
      });
    };

    setData(updateNodeRecursive(data));
    setShowPopup(false);
    setCurrentNode(null);
    setNodeName('');
    setNodeValue('');
  };

  // دالة لإغلاق نافذة التعديل
  const closePopup = () => {
    setShowPopup(false);
    setCurrentNode(null);
    setNodeName('');
    setNodeValue('');
  };

  // دالة لفتح نافذة تأكيد الحذف
  const confirmDeleteNode = (nodeId) => {
    setCurrentNode(findNodeById(data, nodeId));
    setShowDeleteConfirmation(true);
  };

  // دالة لإغلاق نافذة التأكيد
  const closeDeleteConfirmation = () => {
    setShowDeleteConfirmation(false);
    setCurrentNode(null);
  };

  // دالة لإظهار الشجرة
  const renderTree = (nodes) => {
    return (
      <TreeComponent
        data={nodes}
        expanded={expanded}
        selectedNodes={selectedNodes}
        showAdd={showAdd}
        showEdit={showEdit}
        showDelete={showDelete}
        showCheckbox={showCheckbox}
        showValues={showValues}
        nodeField={nodeField}
        toggleExpand={toggleExpand}
        addNode={addNode}
        editNode={editNode}
        confirmDeleteNode={confirmDeleteNode}
        toggleSelectNode={toggleSelectNode}
        renderTree={renderTree}
      />
    );
  };

  return (
    <div>
       {renderTree(data)}

      {/* {showPopup && (
        <div className="popup-overlay">
          <div className="popup">
            <h2>{currentNode ? 'Edit Node' : 'Add Node'}</h2>
            <label>
              {nodeField}:
              <input
                type="text"
                value={nodeName}
                onChange={(e) => setNodeName(e.target.value)}
              />
            </label>
            <br />
            <label>
              Value:
              <input
                type="text"
                value={nodeValue}
                onChange={(e) => setNodeValue(e.target.value)}
              />
            </label>
            <br />
            <button onClick={saveNode}>Save</button>
            <button onClick={closePopup}>Cancel</button>
          </div>
        </div>
      )}

      {showDeleteConfirmation && (
        <div className="popup-overlay">
          <div className="popup">
            <h2>Are you sure you want to delete this node?</h2>
            <button onClick={() => deleteNode(currentNode.id)}>Yes, Delete</button>
            <button onClick={closeDeleteConfirmation}>Cancel</button>
          </div>
        </div>
      )} */}
    </div>
  );
};

export default CustomTree;
