 
import { Button } from 'bootstrap';
import React from 'react';
import { FaChevronDown, FaChevronRight, FaPlus, FaEdit, FaTrash } from 'react-icons/fa';
import {  CustomInput,  Label } from 'reactstrap';


const TreeComponent = ({
  data,
  expanded,
  selectedNodes,
  showAdd,
  showEdit,
  showDelete,
  showCheckbox,
  showValues,
  nodeField,
  toggleExpand,
  addNode,
  editNode,
  confirmDeleteNode,
  toggleSelectNode,
  renderTree,
}) => {
 
  return (
    <ul>
      {data.map((node) => (
         <li key={node?.id}>
          <div className="node-container">
            {node.children.length > 0 ? (
              <a onClick={() => toggleExpand(node?.id)}>
                {expanded[node.id] ? <FaChevronDown /> : <FaChevronRight />}
              </a>
            ):
            (
              <a> - </a>
            )}

            {showCheckbox && (
              <CustomInput
              id={`checkbox-${node?.name}`} 
                type="checkbox"
                checked={selectedNodes?.has(node?.name)}
                onChange={() => toggleSelectNode(node?.name)}
              />
            )}
            <Label onClick={() => editNode(node)} style={{fontSize:"20px",fontWeight:"bold"}}>
              {node[nodeField]} {showValues ? <> - {node.value}</> : null}
            </Label>

            <div className="node-actions">
              {showAdd && (
                <FaPlus onClick={() => addNode(node?.id)} title="Add Child" />
              )}
              {showEdit && (
                <FaEdit onClick={() => editNode(node)} title="Edit" />
              )}
              {showDelete && (
                <FaTrash onClick={() => confirmDeleteNode(node?.id)} title="Delete" />
              )}
            </div>
          </div>
          {node.children.length > 0 && expanded[node?.id] && renderTree(node.children)}
        </li>
      ))}
    </ul>
  );
};

export default TreeComponent;